.faq {
   max-width: 90rem;
   margin: 0 auto;
}

.accordion {
   .accordion__block {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-bottom: 1.3rem;
      margin-bottom: 3rem;

      border-bottom: 2px solid $decorColor;

      .accordion__title {
         font-weight: 600;
         font-size: 1.7rem;
         line-height: 2rem;
         text-align: center;
      }

      .accordion__btn {
         position: relative;

         width: 30px;
         height: 30px;

         background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), #8c96a0;
         border-radius: 6px;

         &::after {
            position: absolute;
            top: 3px;
            left: 10px;

            content: "+";
            display: block;

            font-weight: 600;
            font-size: 2rem;
            line-height: 2.4rem;
            color: $fontColor1;
         }

         &:active::after {
            transform: rotate(45deg);
         }
      }
   }

   .accordion__description {
      padding: 0 0 3.6rem;

      font-size: 1.4rem;
      line-height: 1.7rem;

      span {
         color: red;
      }
   }
}
