.main-header {
   position: relative;
   padding: 5rem 0 10rem;
   margin: 0 auto;

   max-width: 1920px;
   min-height: 70rem;

   &::after,
   &::before {
      position: absolute;
      top: 0;
      right: 0;

      content: "";
      display: block;
      max-width: 136rem;
      width: 100%;
      min-height: 76rem;
   }

   &::after {
      background: url(../images/header-main/heder_main.webp) no-repeat center;
      background-size: cover;
      z-index: -2;
   }

   &::before {
      background: url(../images/header-main/snow.gif) center;
      background-size: contain;
      opacity: 0.2;
      z-index: -1;
   }
}

//header
.header__inner {
   display: flex;
   align-items: center;
   justify-content: space-between;
}

// header__left
.header__left {
   display: flex;
   align-items: center;
}

.header__menu {
   display: flex;
   gap: 3rem;
   margin-left: 2rem;

   .header__link {
      font-size: 1.4rem;
      line-height: 2.7rem;
      text-transform: uppercase;
      color: rgba($fontColor1, 0.5);

      &:hover,
      &:active,
      &:focus {
         color: $fontColor3;
      }
   }
}

//langs
.header__langs {
   position: relative;

   font-size: 1.4rem;
   line-height: 2.7rem;
   font-weight: 700;

   &::after {
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(2rem, 0.8rem);

      display: block;
      content: "";
      width: 12px;
      height: 7px;

      background: url(../images/icons/arrow_.svg) no-repeat center;
   }

   .lang-block {
      position: absolute;

      padding: 1rem;

      background: rgba($bgColor, 0.5);
      color: rgba($fontColor1, 0.8);

      transform: translateY(-10rem);
      opacity: 0;

      transition: transform 0.3s ease-in-out, opacity 0.5s ease-in-out;

      .lang-item:hover,
      .lang-item:focus {
         color: $fontColor3;
      }
   }

   .lang-block-active {
      transform: translateY(0);
      opacity: 1;
   }
}

// header__right
.header__right {
   .header__timer {
      display: flex;
      align-items: center;

      color: $fontColor2;
      font-size: 1.6rem;
      line-height: 1.9rem;
      text-align: center;

      .header__timer-description {
         margin-right: 2rem;
         margin-top: -2rem;
      }

      .timer {
         padding: 0.7rem;

         .timer-value {
            font-weight: 700;
            font-size: 4rem;
            line-height: 4.7rem;
            color: #a9c7c7;
         }
      }
   }
}

// info-header
.info-header {
   padding-top: 12rem;
   padding-bottom: 15rem;
   max-width: 54rem;

   animation: header-anim 1s ease-in-out;

   &__title {
      font-weight: 600;
      font-size: 10rem;
      line-height: 10rem;
   }

   &__description {
      margin-top: 2.5rem;
      margin-bottom: 5rem;

      font-size: 1.8rem;
      line-height: 2.7rem;
      color: $fontColor2;
   }
}

//info-header animation
@keyframes header-anim {
   0% {
      transform: translateX(-70rem);
      opacity: 0;
   }
   100% {
      transform: translateX(0);
      opacity: 1;
   }
}
